$cn: app__project; // cn is for className

.#{$cn} {
  display: flex;
  flex-direction: column;
  width: 270px;

  padding: 1rem;
  border-radius: 0.5rem;
  background-color: #fff;
  color: #000;

  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
  }

  @media screen and (min-width: 2000px) {
    width: 470px;
    padding: 1.25rem;
    border-radius: 0.75rem;
  }

  @media screen and (max-width: 300px) {
    width: 100%;
    margin: 1rem;
  }
}

.#{$cn}-imageContainer {
  width: 100%;
  height: 230px;

  position: relative;

  .#{$cn}-image {
    width: 100%;
    height: 100%;
    border-radius: 0.5rem;
    object-fit: cover;
  }

  @media screen and (min-width: 2000px) {
    height: 350px;
  }
}

.#{$cn}-hover {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);

  border-radius: 0.5rem;
  opacity: 0;
  transition: all 0.3s ease;

  .#{$cn}-linkIconContainer {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.5);
    color: #fff;

    margin: 1rem;
    font-family: var(--font-base);
    font-weight: 800;
    cursor: pointer;
    transition: all 0.3s ease;

    .#{$cn}-linkIcon {
      width: 50%;
      height: 50%;
      color: var(--white-color);
    }
  }
}

.#{$cn}-content {
  padding: 0.5rem;
  width: 100%;
  position: relative;
  flex-direction: column;

  .#{$cn}-title {
    margin-top: 1rem;
    line-height: 1.5;

    @media screen and (min-width: 2000px) {
      margin-top: 3rem;
    }
  }

  .#{$cn}-tag {
    position: absolute;

    padding: 0.5rem 1rem;
    border-radius: 10px;
    background-color: #fff;
    top: -25px;
  }
}

:export {
  cn: $cn;
}