$cn: app__featuredSkill;
$image: #{$cn}-image;
$description: #{$cn}-description;
$mainSection: #{$cn}-mainSection;

.#{$cn} {
  width: 100%;
  display:flex;
  flex-direction: column;
  text-align: center;

  & > * {
    flex: 50%;
  }

  margin: 1rem;
  transition: all 0.3s ease-in-out;
}

.#{$cn}-image {
  border-radius: 50%;
  background-color: #fff;

  width: 90px;
  height: 90px;
}

.#{$cn}-descriptionContainer {
  width: fit-content;
  max-width: 512px;
  margin-bottom: 1rem;
}

.#{$cn}-description {
}

.#{$cn}-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: flex-start;
}

.#{$cn}-index {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.#{$cn}-indexContainer {
  border-radius: 4px !important;
  justify-content: flex-start !important;
  padding: 0 !important;
  padding-right: 1px !important;
}

.#{$cn}-indexList {
  overflow: auto;
  height: 24rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  list-style-type: none;

  & > .Collapsible {
  }

  width: 512px;
}

.#{$cn} .Collapsible {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.#{$cn} .Collapsible__trigger {
  cursor: pointer;
  font-weight: bold;
  color: var(--secondary-color);
  text-align: center;

  &:hover {
    font-weight: bolder;
  }
}

.#{$cn} .Collapsible__contentInner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 1rem;
  text-align: left;
}

.#{$cn}-indexNode-leaf {
  color: var(--secondary-color);
}

.#{$cn}-secondary {
  display: flex;
  flex-direction: row;
  align-items: flex-start;

  & > * {
    width: calc(100% / 2);
    flex-wrap: wrap;
  }
}

.#{$cn}-projectsContainer {
  display: flex;

  .app__featuredSkill-project {
  }
}

.#{$cn}-projects {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.#{$cn}-educationalMaterials {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.#{$cn}-educationalMaterialsContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 2rem 2rem;

  .app__featuredSkill-educationalMaterial {
    //flex: 50%;
  }
}

:export {
  cn: $cn;
  image: $image;
  description: $description;
}