$cn: app__EducationalMaterial; // cn is for className

.#{$cn} {
  display: flex;
  flex-direction: column;
  width: 256px;

  padding: 0.5rem;
  border-radius: 0.5rem;
  background-color: #fff;
  color: #000;
  text-decoration: none;

  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
  }

  &:visited {
    color: #000;
  }

  @media screen and (min-width: 2000px) {
    width: 470px;
    padding: 1.25rem;
    border-radius: 0.75rem;
  }

  @media screen and (max-width: 300px) {
    width: 100%;
    margin: 1rem;
  }
}

.#{$cn}-image {
  display: flex;
  flex-basis: auto;
  max-height: 196px;
  max-width:100%;
  border-radius: 0.5rem;
  object-fit: contain;

  @media screen and (min-width: 2000px) {
    height: 350px;
  }
}

.#{$cn}-hover {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);

  border-radius: 0.5rem;
  opacity: 0;
  transition: all 0.3s ease;
}

.#{$cn}-content {
  padding: 0.25rem 0.5rem;
  width: 100%;
  position: relative;
  flex-direction: column;

  .#{$cn}-name {
    font-weight: 800;
    line-height: 1.5;

    @media screen and (min-width: 2000px) {
      margin-top: 3rem;
    }
  }

  .#{$cn}-authors {
    color: var(--gray-color);
    font-size: 0.75rem;
    font-weight: lighter;
    font-style: italic;
  }

  .#{$cn}-description {
    font-size: 0.8rem;
    text-align: left;
    color: var(--gray-color);
    line-height: 1.5;
  }
}

:export {
  cn: $cn;
}