@import "../../App";

#home {
  position: relative;
  background: url("../../assets/bgIMG.png");
  background-size: cover;
  background-position: center;
  background-repeat: repeat;

  // disable the default padding styles
  .app__wrapper {
    padding: 0;

    .copyright {
      display: none;
    }
  }

  &.app__container {
    min-height: auto;
  }
}

.app__header {
  flex: 1;
  width: 100%;
  height: 100%;
  flex-direction: column;

  padding: 6rem 2rem 0rem;

  @media screen and (min-width: 2000px) {
    padding-top: 8rem;
  }

  @media screen and (max-width: 1200px) {
    flex-direction: column;
  }

  @media screen and (max-width: 450px) {
    padding: 6rem 1rem 2rem;
  }
}

.app__header-main {
  flex: 1;
  width: 100%;
  height: 100%;
  flex-direction: row;

  padding: 6rem 2rem 0rem;

  @media screen and (min-width: 2000px) {
    padding-top: 8rem;
  }

  @media screen and (max-width: 1200px) {
    flex-direction: column;
  }

  @media screen and (max-width: 450px) {
    padding: 6rem 1rem 2rem;
  }


  z-index: 1;
}

.app__header-footer {
  //position: absolute;
  bottom: 0;
  z-index: 0;
}

.app__header-info {
  flex: 0.65;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 100%;

  margin: 0 2rem;

  @media screen and (max-width: 1200px) {
    width: 100%;
    margin-right: 0rem;
  }
}

.app__header-badge {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;

  text-align: center;

  &-greeting {
    @extend .app__flex;
    flex-direction: row;
    width: auto;
  }

  &-meetingText2-container {
    margin-top: 3rem;
  }

  &-meetingText2 {
    @extend .p-text;
    width: 100%;
    text-align: right;
    font-size: 1rem;
  }

  span {
    font-size: 2.5rem;

    @media screen and (min-width: 2000px) {
      font-size: 5rem;
    }
  }

  @media screen and (max-width: 1200px) {
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.app__header-circles {
  flex: 0.75;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 100%;
  margin-left: 1rem;

  .circle-cmp {
    display: flex;
    width: 100px;
    height: 100px;
    border-radius: 50%;

    background: var(--white-color);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);

    img {
      width: 80%;
      height: 80%;
    }
  }

  // circles with different height & width

  .circle-cmp:nth-of-type(1) {
    width: 100px;
    height: 100px;
  }

  .circle-cmp:nth-of-type(2) {
    margin: 1.75rem;
    width: 125px;
    height: 125px;
  }

  .circle-cmp:nth-of-type(3) {
    margin: 1.75rem;
    width: 150px;
    height: 150px;
  }

  .circle-cmp:nth-of-type(4) {
    width: 80px;
    height: 80px;
  }

  @media screen and (min-width: 2000px) {
    .circle-cmp:nth-child(1) {
      width: 200px;
      height: 200px;
    }

    .circle-cmp:nth-child(2) {
      width: 400px;
      height: 400px;
    }

    .circle-cmp:nth-child(3) {
      width: 400px;
      height: 400px;
    }

    .circle-cmp:nth-child(4) {
      width: 170px;
      height: 170px;
    }
  }

  @media screen and (max-width: 1200px) {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: 0;

    .circle-cmp {
      margin: 1rem;
    }
  }
}

.app__header-img {
  flex: 1;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;

  img {
    justify-self: center;
    width: 50%;
    object-fit: contain;
    z-index: 1;

    @media screen and (max-width: 1200px) {
      width: 100%;
    }
  }

  .overlay_circle {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 90%;
  }

  @media screen and (max-width: 1200px) {
    margin: 2rem 0;
  }
}

.app__header-footer {
  text-align: center;
}

.app__header-footerImage {
  max-width: 860px;
  width: 100%;
  margin: 0 auto;
}

.header-circle-tooltip {
  max-width: 300px !important;
  background-color: var(--white-color) !important;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.1) !important;
  border-radius: 5px !important;
  padding: 1rem !important;
  color: var(--gray-color) !important;
  text-align: center !important;
  line-height: 1.5 !important;
  opacity: 1 !important;

  @media screen and (min-width: 2000px) {
    font-size: 1.75rem !important;
    max-width: 500px !important;
    line-height: 2 !important;
  }
}